.Experience {
  text-align: center;
}
.experience {
  &__list {
    display: flex;
    flex-direction: column;
    text-align: left;

    &__item {
      margin: 1.25rem 0;

      &:not(:last-of-type) {
        border-bottom: 1px solid rgba(255, 255, 255, 0.8);
      }

      ul {
        padding-left: 0;
        list-style: none;

        li {
          &:before {
            content: "- ";
          }
        }
      }
    }
  }
}
